// src/template.js

//  :  HEX (#00FF9D)  RGB (0, 255, 157)   
const hexToRgb = (hex) => {
  let c;
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split('');
    if (c.length === 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]];
    }
    c = '0x' + c.join('');
    return {
      r: (c >> 16) & 255,
      g: (c >> 8) & 255,
      b: c & 255
    };
  }
  return { r: 0, g: 255, b: 157 }; //       
};

const getNavLinks = (currentLang) => {
  if (currentLang === 'en') {
    return `
      <a href="index.html" class="active">EN</a>
      <a href="ru/index.html">RU</a>
      <a href="de/index.html">DE</a>
    `;
  } else {
    return `
      <a href="../index.html">EN</a>
      <a href="index.html" class="${currentLang === 'ru' ? 'active' : ''}">RU</a>
      <a href="../de/index.html" class="${currentLang === 'de' ? 'active' : ''}">DE</a>
    `;
  }
};

export const generateHTML = (data) => {
  const aboutListArray = data.aboutListRaw
    ? data.aboutListRaw.split('\n').filter(line => line.trim() !== '')
    : [];

  //        
  const mainColor = data.accentColor || '#00FF9D';
  const rgb = hexToRgb(mainColor);

  return `<!DOCTYPE html>
<html lang="${data.lang}">
<head>
<meta charset="UTF-8">
<meta name="viewport" content="width=device-width, initial-scale=1.0">
<title>${data.pageTitle}</title>

<link href="https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800;900&family=Inter:wght@400;500;600&display=swap" rel="stylesheet">

<style>
:root {
  /*    */
  --neo-green: ${mainColor};
  /*  glow-  RGB   */
  --neo-glow: 0 0 22px rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, 0.35);
  
  --bg-black: #000;
  --bg-card: #0F0F0F;
  --text-white: #fff;
  --text-muted: #B8B8B8;
  --radius: 14px;
  --font-main: 'Montserrat', sans-serif;
  --font-alt: 'Inter', sans-serif;
}

* { margin: 0; padding: 0; box-sizing: border-box; }
html, body { width: 100%; min-height: 100%; }
body {
  background: var(--bg-black);
  color: var(--text-white);
  font-family: var(--font-main);
  line-height: 1.55;
}

/* ---------- LAYOUT ---------- */
.page { width: 100%; }
section { width: 100%; padding: 80px 60px; }
.container { width: 100%; max-width: none; margin: 0; }

/* ---------- HEADER ---------- */
.lang-switcher { width: 100%; text-align: right; padding: 20px 60px; }
.lang-switcher a {
  margin-left: 15px;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  color: var(--text-muted);
  transition: color 0.3s;
}
.lang-switcher a:hover, .lang-switcher a.active {
  color: var(--neo-green);
}

/* ---------- HERO ---------- */
.hero { text-align: center; }
.badge {
  display: inline-block; padding: 10px 22px; background: var(--neo-green);
  color: #000; font-weight: 700; border-radius: 30px; font-size: 14px; margin-bottom: 30px;
}
.hero h1 { font-size: 64px; font-weight: 900; line-height: 1.1; margin-bottom: 24px; }
.hero p { font-size: 20px; color: var(--text-muted); margin-bottom: 18px; }

/* ---------- AVATAR ---------- */
.avatar-section { text-align: center; }
.section-badge {
  display: inline-block; padding: 10px 22px; background: var(--neo-green);
  color: #000; font-weight: 700; border-radius: 30px; font-size: 14px; margin-bottom: 30px;
}
.avatar-frame {
  max-width:720px; margin: 40px auto 0;
  border-radius: var(--radius); overflow: hidden;
  border: 2px solid var(--neo-green); box-shadow: var(--neo-glow);
}
.avatar-frame iframe { width: 100%; border: none; aspect-ratio:16/9; }

/* ---------- ABOUT ---------- */
.list { margin-top: 30px; list-style: none; text-align: center; }
.list li { margin-bottom: 14px; font-size: 20px; color: var(--text-muted); }

/* ---------- CONTACT ---------- */
.form-wrapper {
  width: 100%; max-width: 820px; margin: 0 auto; background: var(--bg-card);
  padding: 50px; border-radius: var(--radius); border: 1px solid #1A1A1A; text-align: center;
}
.options { display: flex; flex-direction: column; gap: 16px; margin: 30px 0; }
.option {
  padding: 18px; border-radius: 14px; background: #000; border: 1px solid #222;
  cursor: pointer; transition: .2s;
}
.option.active {
  border-color: var(--neo-green); 
  /*    */
  box-shadow: 0 0 0 1px rgba(${rgb.r},${rgb.g},${rgb.b},0.4), var(--neo-glow);
}
.icons { display: flex; justify-content: center; gap: 32px; margin-top: 30px; }
.icon-btn {
  width: 64px; height: 64px; border-radius: 50%; background: #000;
  display: flex; flex-direction: column; align-items: center; justify-content: center; text-decoration: none;
}
.whatsapp { color: #25d366; box-shadow: 0 0 22px rgba(37,211,102,.45); }
.telegram { color: #2aabee; box-shadow: 0 0 22px rgba(42,171,238,.45); }
.icon-label { font-size: 10px; color: #9aa0b5; margin-top: 4px; }

/* ---------- FOOTER ---------- */
footer { width: 100%; padding: 60px; text-align: center; background: #080808; }
footer a { color: var(--neo-green); text-decoration: none; }

/* ---------- MOBILE ---------- */
@media (max-width: 900px) {
  section { padding: 60px 24px; }
  .lang-switcher { padding: 20px 24px; }
  .hero h1 { font-size: 42px; }
  
}
</style>
</head>

<body>
<div class="page">
  <div class="lang-switcher">
    ${getNavLinks(data.lang)}
  </div>

  <section class="hero">
    <div class="badge">${data.heroBadge}</div>
    <h1>${data.heroTitle}</h1>
    <p>${data.heroText1}</p>
    <p>${data.heroText2}</p>
    <p>${data.heroText3}</p>
  </section>

  <section class="avatar-section">
    <div class="section-badge">${data.avatarBadge}</div>
    <h2 style="font-size:42px;margin-bottom:20px;">${data.avatarTitle}</h2>
    <p style="font-size:20px;color:var(--text-muted);margin-bottom:40px;">
      ${data.avatarDescription}
    </p>
    <div class="avatar-frame">
      <iframe src="${data.avatarUrl}" allow="microphone; camera"></iframe>
    </div>
  </section>

  <section>
    <h2 style="text-align:center;font-size:42px;">${data.aboutTitle}</h2>
    <ul class="list">
      ${aboutListArray.map(item => `<li>${item}</li>`).join('')}
    </ul>
  </section>

  <section>
    <div class="form-wrapper">
      <h2>${data.contactTitle}</h2>
      <p>${data.contactSubtitle}</p>

      <div class="options">
        <div class="option active" data-interest="${data.option1}">${data.option1}</div>
        <div class="option" data-interest="${data.option2}">${data.option2}</div>
        <div class="option" data-interest="${data.option3}">${data.option3}</div>
      </div>

      <div class="icons">
        <a id="waLink" class="icon-btn whatsapp" target="_blank">
          <div class="icon-label">WhatsApp</div>
        </a>
        <a id="tgLink" class="icon-btn telegram" target="_blank">
          <div class="icon-label">Telegram</div>
        </a>
      </div>

      <p style="margin-top:20px;font-size:12px;color:#7f859a;">
        ${data.contactFooter}
      </p>
    </div>
  </section>

  <footer>
    <a href="${data.presentationLink}" target="_blank">${data.downloadText}</a>
    <p>${data.copyright}</p>
  </footer>
</div>

<script>
const options = document.querySelectorAll('.option');
const waLink = document.getElementById('waLink');
const tgLink = document.getElementById('tgLink');

let selectedInterest = '${data.option1}';
const whatsappNumber = '${data.whatsappNumber}';
const telegramUser = '${data.telegramUsername}';

function updateLinks() {
  const msg = encodeURIComponent('${data.messagePrefix} ' + selectedInterest);
  waLink.href = 'https://wa.me/' + whatsappNumber + '?text=' + msg;
  tgLink.href = 'https://t.me/' + telegramUser + '?text=' + msg;
}

options.forEach(o => {
  o.onclick = () => {
    options.forEach(x => x.classList.remove('active'));
    o.classList.add('active');
    selectedInterest = o.dataset.interest;
    updateLinks();
  };
});

updateLinks();
</script>

</body>
</html>`;
};